}

/* custom classes */

.react-datepicker__navigation:hover {
  background: #f7f7f7;
}

.react-datepicker__navigation:before {
  display: block;
  line-height: 40px;
  width: 40px;
  height: 40px;
  text-align: center;
  text-indent: 1px;
  font-size: 16px;
}

.react-datepicker__navigation--previous:before {
